"use client";
import Image from "next/image";
import NameIntro from "../intro";
import { useState } from "react";
import MagicWords from "./magic-words";
import InfiniteCarousel from "./infinite-carousel";
import CostMaintainance from "./cost-maintainance";

const Hero = () => {
  const [intro, setIntro] = useState(true);
  const [showCarousal, setShowCarousal] = useState(false);

  if (intro) return <NameIntro intro={intro} setIntro={setIntro} />;
  return (
    <section className=" overflow-x-hidden flex flex-col gap-y-11">
      <div className="h-screen flex flex-col">
        <div className="flex  justify-center mt-24  ">
          <Image
            className="rounded-full"
            src={"/profilepicture.jpg"}
            alt="KK"
            width={150}
            height={150}
            priority
          />
        </div>
        <MagicWords setShowCarousal={setShowCarousal} />
        {showCarousal && <InfiniteCarousel />}
      </div>
      <CostMaintainance />
    </section>
  );
};
export default Hero;
